tainer {\r\n    margin: auto;\r\n    max-width: 1200px;\r\n}\r\n.body-cards {\r\n    background-color: $cardsBGC;\r\n}\r\n\r\n.container-cards {\r\n    // padding: 150px 120px 140px 140px;\r\n    padding-top: 150px;\r\n    padding-bottom: 140px;\r\n}\r\n.dropdown-test{\r\n    margin-top: 100px;\r\n}\r\n\r\n.pageLogo {\r\n    position: absolute;\r\n    left: 30px;\r\n    top: 30px;\r\n\r\n}\r\n\r\n.colorAndTextTypes {\r\n    display: flex;\r\n    justify-content: space-between;\r\n    padding-top: 150px;\r\n    padding-bottom: 150px;\r\n}\r\n\r\n\r\n\r\n\r\n.color__item-box {\r\n    border-radius: 6px;\r\n    width: 70px;\r\n    height: 70px;\r\n    margin-right: 40px;\r\n}\r\n\r\n.ds100 {\r\n    background-color: $ds100;\r\n}\r\n\r\n.ds75 {\r\n    background-color: $ds75;\r\n}\r\n\r\n.ds50 {\r\n    background-color: $ds50;\r\n}\r\n\r\n.ds25 {\r\n    background-color: $ds25;\r\n}\r\n\r\n.ds5 {\r\n    background-color: $ds5;\r\n}\r\n.prpl {\r\n    background-color: $purple;\r\n}\r\n.grn {\r\n    background-color: $green;\r\n}\r\n\r\n.color__item {\r\n    margin-bottom: 20px;\r\n    display: flex;\r\n    align-items: center;\r\n}\r\n\r\n.color__item-id {\r\n    font-weight: 400;\r\n}\r\n\r\n.textTypes {\r\n    max-width: 436px;\r\n}\r\n\r\n.textTypes__item {\r\n    display: flex;\r\n    margin-bottom: 51px;\r\n}\r\n\r\n.title__h1,\r\n.title__h2,\r\n.title__h3,\r\n.title__p {\r\n    color:$ds25;\r\n    font-weight: 400;\r\n}\r\n\r\n\r\n.textTypes__item-title {\r\n    margin-right: 30px;\r\n    width: 37px;\r\n    text-align: right;\r\n}\r\n\r\n.textTypes__item-text {\r\n    text-align: left;\r\n}\r\n\r\n\r\n\r\n.select {\r\n    width: 320px;\r\n    height: 44px;\r\n    border: 1px solid $ds25;\r\n    border-radius: 4px;\r\n    text-align-last: left;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: space-between;\r\n    background-color: #fff;\r\n    transition: border .3s;\r\n}\r\n\r\n\r\n.selected {\r\n    color:$ds75;\r\n    font-size: 14px;\r